.wmde-markdown {
  font-size: 14px;
  > :first-child {
    margin-top: 0 !important;
  }
  > :last-child {
    margin-bottom: 0 !important;
  }
  code[class*="language-"], pre[class*="language-"] {
    color: black;
    text-align: left;
    white-space: pre;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    hyphens: none;
  }

  pre code {
    background: none;
    overflow-wrap: normal;
    white-space: inherit;
    tab-size: 2;
    padding: 16px;
    font-size: 95%;
    line-height: 1.5;
    display: block;
    text-shadow: 0 1px #fff;
    font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  }

  pre {
    margin-bottom: 18px;
    font-size: 85%;
    line-height: 1.45;
    position: relative;
    overflow-x: auto;
    background-color: #f6f8fa;
    border-radius: 3px;
  }
  code, tt {
    background-color: rgba(27,31,35,.05);
    border-radius: 3px;
    font-size: 85%;
    margin: 0;
    padding: .2em .4em;
  }
  h1, h2 {
    border-bottom: 1px solid #eaecef;
    padding-bottom: .3em;
  }
  h1, h2, h3, h4, h5, h6 {
    font-weight: 600;
    line-height: 1.25;
    margin-bottom: 16px;
    margin-top: 24px;
  }
  h1 {
    font-size: 2em;
  }
  h2 {
    font-size: 1.5em;
  }
  h3 {
    font-size: 1.25em;
  }
  h4 {
    font-size: 1em;
  }
  h5 {
    font-size: .875em;
  }
  h6 {
    font-size: .85em;
  }
  ol, ul {
    padding-left: 2em;
  }
  ul {
    margin-bottom: 16px;
    margin-top: 0;
  }
  blockquote {
    margin: 0;
    border-left: .25em solid #dfe2e5;
    color: #6a737d;
    padding: 0 1em;
    > :last-child {
      margin-bottom: 0;
    }
    > :first-child {
      margin-top: 0;
    }
  }

  hr {
    margin: 1.5em auto;
    border: 0;
    border-top: 2px dotted #eee;
    height: 1px;
  }

  table {
    display: block;
    overflow: auto;
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    font-size: 14px;
    tr {
      background-color: #fff;
      border-top: 1px solid #c6cbd1;
    }
    td, th {
      border: 1px solid #dfe2e5;
      padding: 3px 12px;
    }
  }
  blockquote, details, dl, ol, p, pre, table, ul {
    margin-bottom: 16px;
    margin-top: 0;
  }

  .token.comment,
  .token.block-comment,
  .token.prolog,
  .token.doctype,
  .token.cdata {
    color: #7D8B99;
  }

  .token.punctuation {
    color: #a0a0a0;
  }

  .token.property,
  .token.tag,
  .token.boolean,
  .token.number,
  .token.function-name,
  .token.constant,
  .token.symbol,
  .token.deleted {
    color: #c92c2c;
  }

  .token.selector,
  .token.attr-name,
  .token.string,
  .token.char,
  .token.function,
  .token.builtin,
  .token.inserted {
    color: #6f42c1;
  }

  .token.operator,
  .token.entity,
  .token.url,
  .token.variable {
    color: #d73a49;
    background: rgba(255, 255, 255, 0.5);
  }

  .token.atrule,
  .token.attr-value,
  .token.keyword,
  .token.class-name {
    color: #1990b8;
  }
  .token.class-name {
    color: #6f42c1;
  }

  .token.regex,
  .token.important {
    color: #e90;
  }

  .token.string {
    color: #ff5722;
  }

  .token.important {
    font-weight: normal;
  }

  .token.bold {
    font-weight: bold;
  }
  .token.italic {
    font-style: italic;
  }

  .token.entity {
    cursor: help;
  }

  .namespace {
    opacity: .7;
  }
}